import Vue from 'vue';
import Router from 'vue-router';
import upload from '@/components/upload';
import progress from '@/components/progress';
Vue.use(Router);

export default new Router({
    routes: [
        {
            path: '/',
            component: upload
        },
        {
            path: '/pro',
            component: progress
        }
    ]
});



