<template>
  <div>
    <h1>{{info}}</h1>
    <div id='outer'>
      <label for="file" v-on:mousedown='addshadow' v-on:mouseup='removeshadow'>点击选择</label>
      <div class='content'>
        <input v-show='false' :disabled='disable' ref='inputFile' v-on:change='getfiles' type="file" id='file' multiple>
        <div class='list' id='listForFiles'>
          <div>
            <span>名字</span>
            <span>大小</span>
            <span>属性</span>
          </div>
          <div v-for='data in files'>
            <span>{{data.name}}</span>
            <span>{{getTotalSize(data.size)}}</span>
            <span>{{getType(data.type)}}</span>
          </div>
        </div>
      </div>
      <input v-show='false' type='button' id=button></input>
      <label v-on:mousedown='addshadow' v-on:mouseup='removeshadow' for='button' v-on:click='sendFiles'>传送</label>
    </div>
    <div class='highest'></div>
    <div class=container>
      <prog v-bind:perc='percent'></prog>
    </div>
  </div>
</template>

<script>
import prog from '@/components/progress'
export default {
  name: 'hello',
  data() {
    return {
      disable: false,
      info: '可以上传多个文件...',
      files: [],
      percent: 0,
      url: '/upload'
    }
  },
  methods: {
    addshadow(e) {
      e.target.classList.add('click')
      if (e.target.getAttribute("target") === 'file') this.info = '选择文件中。。。。'
    },
    removeshadow(e) {
      e.target.classList.remove('click')
    },
    getfiles() {
      this.files = Array.from(this.$refs.inputFile.files);
      if (this.files.length === 0) {
        this.info = '请选择文件再传送。。。'
        return
      }
      this.info = `你已经选择了${this.files.length}个文件,可以按传送开始传送`
    },
    getTotalSize(num) {
      if (Math.floor(num / 1024) === 0) {
        return num + 'b'
      } else if (Math.floor(num / 1024) < 1024) {
        return Math.round(num / 1024) + 'kb'
      } else {
        return Math.round(num / 1024 / 1024) + 'M'
      }
    },
    getType(str) {
      return /\//.test(str) ? str.split('/')[1] : str
    },
    _xhr() {
      this.info = '正在传送文件，不可以再选择文件，请等文件传送完毕。。'
      this.disable = true
      let formdata = new FormData()
      this.files.forEach(_file => {
        formdata.append(_file.name, _file)
      })
      let xhr = new XMLHttpRequest()
      xhr.open('POST', this.url, true);
      xhr.onreadystatechange = () => {
        if (xhr.status != 204) {
          this.info = '出现错误，请联系我'
        }
      }
      xhr.upload.onloadstart = () => {
        window.onbeforeunload = (e) => {
          return void alert('uploading');
        }
      }

      xhr.upload.onabort = () => {
        this.info = '出现错误，可以刷新重试，也可以联系我'
      }

      xhr.upload.onprogress = (e) => {
        e = e || window.event;
        this.percent = e.loaded / e.total;
        if (this.percent === 1) {
          this.info = '上传完成'
          this.files = []
          this.disable = false
        }
      }
      xhr.send(formdata);
    },
    sendFiles() {
      if (this.disable) {
        return false
      }
      if (this.files.length === 0) {
        this.info = '请选择文件再传送。。。'
      } else {
        this._xhr()
      }
    },
  },
  mounted() {
  },
  components: {
    prog
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped >
#outer {
  width: 80vw;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

h1 {
  padding: 10px;
  margin: 5vw;
  font-size: 1.9vw;
  color: #E6E1D8;
  text-align: center;
  background-color: #61B8EB;
  border-top-left-radius: 10% 100%;
  border-top-right-radius: 10% 100%;
  box-shadow: 0 1px 1px 1px #3C7EA5;
}

label {
  padding: 10px;
  background-color: rgba(46, 128, 221, 0.5);
  border-radius: 10%;
}

.content {
  width: 70%;
}

.list {
  border-radius: 12px;
  overflow: hidden;
}




.list>div {
  padding: 10px;
}

.list span {
  display: inline-block;
  text-align: center;
}

.list>div>span:nth-child(1) {
  width: 19%
}

.list>div>span:nth-child(2) {
  width: 59%;
}

.list>div>span:nth-child(3) {
  width: 19%;
}

.list>div:nth-child(even) {
  background-color: rgba(0, 150, 255, 0.52)
}

.list>div:nth-child(odd) {
  background-color: rgba(0, 225, 255, 0.47)
}

.click {
  box-shadow: 0 0 1px 5px rgba(18, 28, 109, 0.51) inset
}

.container {
  position: relative;
  border-radius: 50%;
  backgroun-clip: content-box;
  width: 30vw;
  height: 25vw;
  margin: auto;
}

.container svg {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
}

.highest {
  margin: 3vw auto 0;
  height: 5vh
}
</style>



