<template>
  <svg id="svg" width="100%" height="100%" preserveAspectRatio="none" viewBox='0,0,100,100'>
    <path v-bind:d='d' fill='#0ebef9' fill-opacity="0.75"></path>
    <path id='topLeft' d='M50 0 L0 0 L0 50 Q0 0 50 0' fill='white'></path>
    <path id='topRight' d='M50 0 L100 0 L100 50 Q100 0 50 0' fill='white'></path>
    <path id='bottomLeft' d='M0 50L0 100 L50 100 Q0 100 0 50' fill='white'></path>
    <path id='bottomRight' d='M100 50L100 100L50 100Q100 100 100 50' fill='white'></path>
    <path id='border' d='M50 0Q0 0 0 50Q0 100 50 100Q100 100 100 50Q100 0 50 0' fill="none" stroke="#0ab8f3" stroke-width="1"></path>
  </svg>
</template>
<script>
export default {
  name: 'progress',
  props: ['perc'],
  data() {
    return {
      qx: 95, //基准点的位子，这是第一个，感兴趣可以仔细看上面的html
      range: 10, //波浪的摇摆幅度
    }
  },
  methods: {
    _flow() {
      const side_right = this.qx + this.range / 2
      const side_left = this.qx - this.range / 2
      let add = true
      const render = () => {
        if (add) {
          this.qx += 0.1
        } else {
          this.qx -= 0.1
        }
        if (this.qx >= side_right) {
          add = false
        } else if (this.qx <= side_left) {
          add = true
        }
        requestAnimationFrame(render)
      }
      render()
    },
  },
  mounted() {
    this._flow()
  },
  computed: {
    bottom() {
      return 100 - this.perc * 100
    },
    d() {
      return 'M0 ' + this.bottom + 'L0 100' + 'L100 100' + 'L100 ' + this.bottom
        + 'Q' + this.qx + ' ' + (this.bottom + 10)
        + ' 90 ' + this.bottom
        + 'T80 ' + this.bottom
        + 'T70 ' + this.bottom
        + 'T60 ' + this.bottom
        + 'T50 ' + this.bottom
        + 'T40 ' + this.bottom
        + 'T30 ' + this.bottom
        + 'T20 ' + this.bottom
        + 'T10 ' + this.bottom
        + 'T0 ' + this.bottom
    }
  }
}
</script>
<style>

</style>




